import React, { useEffect } from "react";
import Typography from "@mui/material/Typography";
// import Link from '@mui/material/Link';
import Container from "@mui/material/Container";
import { getLang, useHash } from "../../helper/utils";
import { useSearchParams } from "react-router-dom";
import Page from "../../layout/Page";

const SuccessPage = (props) => {
	const [searchParams] = useSearchParams();
	const urlParams = new URLSearchParams(window.location.search);

	let lang = urlParams.get("lang");
	if (!lang) lang = searchParams.get("lang");
	if (!lang) lang = navigator.language || navigator.userLanguage;

	window.__current_language = lang;
	const languages = getLang();
	const texts = languages.pages.success_page;
	return (
		<Page>
				<Container
					disableGutters
					maxWidth="lg"
					component="section"
					className="section bg--blue"
				>
					<div className="row">
						<div className="col col--1">

							<Typography
								component="h2"
								variant="h4"
								align="center"
								gutterBottom
								sx={{ fontWeight: 500, mb: "1rem", pb: "0rem" }}
							>
								<span
									style={{
										textDecoration: "underline",
										textDecorationColor: "var(--unikbase-red)",
									}}
								>
									{texts.title}
								</span>
							</Typography>
							<Typography
								variant="h4"
								align="center"
								component="p"
								sx={{
									mt: "1em",
									mb: "1em",
									fontWeight: 500,
								}}
							>
								{texts.sub_title}
							</Typography>

							<Typography
								component="div"
								variant="p"
								align="center"
								sx={{ mb: "1em", fontSize: "20px", fontWeight: 400 }}
							>
								<div dangerouslySetInnerHTML={{ __html: texts.description }}></div>
							</Typography>

							{texts.email && (
								<Typography
									variant="p"
									align="center"
									component="div"
									sx={{ mb: "1em", fontSize: "20px", fontWeight: 400 }}
								>
									{texts.email_text}{" "}
									<a
										style={{ color: "#fff", textDecorationColor: "#EA5123", textDecoration: "underline" }}
										href={`mailto:${texts.email}`}
										target="_blank"
										rel="noreferrer"
										title={`Email to ${texts.email}`}
									>
										{texts.email}
									</a>
								</Typography>
							)}
						</div>
					</div>
				</Container>
		</Page>
	);
};

export default SuccessPage;
