import React, { useEffect } from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Typography from "@mui/material/Typography";
// import Link from '@mui/material/Link';
import GlobalStyles from "@mui/material/GlobalStyles";
import Container from "@mui/material/Container";

import useHttp from "../../hooks/use-Http";
import { getLang, useHash } from "../../helper/utils";
import Header from "../../layout/Header";
import Footer from "../../layout/Footer";
import { useSearchParams } from "react-router-dom";
import Page from "../../layout/Page";

const STRIPE_CANCEL_URL = process.env.REACT_APP_STRIPE_CANCEL_URL || "https://dev1.unikbase.dev/meveo/rest/cancelStripeSession";

const CancelPage = (props) => {

	const [searchParams] = useSearchParams();
	const urlParams = new URLSearchParams(window.location.search);

  let lang = urlParams.get('lang');
  if ( !lang ) lang = searchParams.get('lang');
  if ( !lang ) lang = navigator.language || navigator.userLanguage; 

	window.__current_language = lang;

  const languages = getLang();
  const texts = languages.pages.cancel_page;

	let params = {};

	let ses_id = localStorage.getItem("session_id");
	if ( ses_id ) {
		params = localStorage.getItem(`_tpk_request_${ses_id}`);
		try {
			params = JSON.parse(params)
		} catch (e) {
			params = {}
		}
	}

	// Update return link
	let sub_title = texts.sub_title;
	if ( params ) {
		sub_title = sub_title.replace('href="/"', `href="/?tpk_id=${params.tpk_id}&value=${params.value}&image=${encodeURIComponent(params.image)}"`);
	}

	const {
		isLoading: isLoading,
		hasError: httpError,
		sendRequest: sendRequest,
		setIsLoading: setIsLoading,
		setHttpError: setHttpError,
	} = useHttp({ url: `${STRIPE_CANCEL_URL}`, method: "POST" });

	useEffect(() => {
		if (ses_id) {
			sendRequest({
				"sessionId": ses_id
			})
				.catch((error) => {
					setIsLoading(false);
					setHttpError(error.message);
				})
				.then((url) => {
					console.log(url);
				});
			// console.log("CANCELED SUCCESSFULLY");
			//  localStorage.clear();
			ses_id = null;
		}
	}, ses_id);

	return (
		<Page>
			<Container
				maxWidth="lg"
				className="section bg--blue"
				component="section"
			>
				<div className="row">
					<div className="col col--1">

						<Typography
							component="h2"
							variant="h4"
							align="center"
							gutterBottom
							sx={{
								fontWeight: 500,
								mb: "2em",
							}}
						>
							{texts.title}
						</Typography>
						<Typography
							align="center"
							variant="h4"
							sx={{ mb: "1em", fontWeight: 400 }}
						>
							<div className="" dangerouslySetInnerHTML={{__html: sub_title}}></div>
						</Typography>
					</div>
				</div>
			</Container>
		</Page>
	);
}

export default CancelPage;
